<template>
  <div id="login">
    <div id="loginBox">
      <h2>感谢注册！</h2>
      <el-form :model="form" :rules="loginRules" ref="form" label-width="0px">
        <el-form-item label prop="username" style="margin-top:40px;">
          <el-row>
            <el-col :span="2">
              <i class="iconfont el-icon-user"></i>
            </el-col>
            <el-col :span="22">
              <el-input class="inps" placeholder="请输入学号！" v-model="form.username"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label prop="password">
          <el-row>
            <el-col :span="2">
              <i class="iconfont el-icon-goods"></i>
            </el-col>
            <el-col :span="22">
              <el-input class="inps" placeholder="密码" v-model="form.password"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label prop="rePassWord">
          <el-row>
            <el-col :span="2">
              <i class="iconfont el-icon-goods"></i>
            </el-col>
            <el-col :span="22">
              <el-input class="inps" placeholder="确认密码" v-model="form.rePassWord"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label prop="email">
          <el-row>
            <el-col :span="2">
              <i class="iconfont el-icon-message"></i>
            </el-col>
            <el-col :span="8">
              <el-input class="inps" placeholder="邮箱" v-model="form.email"></el-input>
            </el-col>
            <el-col :span="6">
              <el-button @click="getEmailCode" type="small" round>获取邮箱验证码</el-button>
            </el-col>
            <el-col :span="6">
              <el-form-item label prop="emailCode">
                <el-row>
                  <el-col :span="2">
                    <i class="iconfont el-icon-goods"></i>
                  </el-col>
                  <el-col :span="22">
                    <el-input class="inps" placeholder="邮箱验证码" v-model.number="form.emailCode"></el-input>
                  </el-col>
                </el-row>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label prop="sex">
          <el-row>
            <el-col :span="2">
              <i class="iconfont el-icon-male"></i>
            </el-col>
            <el-col :span="22">
              <el-radio v-model="form.sex" label="0">男</el-radio>
              <el-radio v-model="form.sex" label="1">女</el-radio>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item style="margin-top:55px;">
          <el-button type="primary" round class="submitBtn" @click="submitForm">注册</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    var validatePass2 = (rule, rePassWord, callback) => {
      // 校验确认密码
      if (rePassWord === "") {
        callback(new Error("请再次输入密码"));
      } else if (rePassWord !== this.form.passWord) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    var checkEmail = (rule, email, callback) => {
      // 校验邮箱
      const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
      if (!email) {
        return callback(new Error("邮箱不能为空"));
      }
      setTimeout(() => {
        if (mailReg.test(email)) {
          callback();
        } else {
          callback(new Error("请输入正确的邮箱格式"));
        }
      }, 100);
    };
    var validateemailCode = (rule, emailCode, callback) => {
      // 校验验证码
      if (!emailCode) {
        callback(new Error("请输入验证码"));
      } else if (emailCode.toString().length !== 4) {
        callback(new Error("请输入4位验证码!"));
      } else {
        callback();
      }
    };
    return {
      shadowColorList: [
        "#39f",
        "#ec5707",
        "#b031d4",
        "#22e6c7",
        "#92d819",
        "#14d7f1",
        "#e23c66"
      ], //阴影颜色列表
      form: {
          username: '',
          passWord: '',
          rePassWord: '',
          email: '',
          emailCode: null,
          sex: '0'
      },
      reqData: {},
      loginRules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" }
        ],
        passWord: [{ required: true, message: "请输入密码", trigger: "blur" }],
        rePassWord: [
          { validator: validatePass2, trigger: "blur" }
        ],
        email: [{ required: true, message: "请输入验证邮箱", trigger: "blur" },
        { validator: checkEmail, trigger: "blur" }],
        emailCode: [
          { validator: validateemailCode, trigger: "blur" },
          { type: "number", message: "验证码必须是数字" }
        ]
      }
    };
  },
  methods: {
    //获取验证码
    getEmailCode() {
      console.log('getEmailCode');
      this.$message({
        message: "正在获取邮箱验证码，请稍等！",
        type: "success"
      });
      this.$axios({
        method: "post",
        url: "/api/user/validationemail",
        data: {
          email: this.form.email
        }
      })
        .then(res => {
          console.log(res);
          if (res.data.msg === "邮件已发送") {
            this.$message({
              message: "验证码发送成功，请查收！",
              type: "success"
            });
            return (this.form.emailCode = res.data.student.verificationCode);
          } else {
            this.$message({
              //bug
              message: "验证码发送失败",
              type: "error"
            });
          }
        })
        .catch(err => {
            alert('网络异常，请重试！');
        });
    },
    //提交登录
    submitForm() {
      console.log('submitForm');
      }
    },
};
</script>

